import {optionify} from '@kevinastone/result';

import search, {loader, SearchResult as TSearchResult} from './lunr';

export type SearchResult = TSearchResult;

export default async function(query: string): Promise<TSearchResult[]> {
  const data = (await loader<SearchResult>()).map(index => index.en);

  return optionify(query)
    .filter(Boolean)
    .andThen(query => data.map(({index, store}) => ({index, store, query})))
    .map(({index, store, query}) => search(index, store, query))
    .unwrapOr([]);
}
