import {optionify, Option} from '@kevinastone/result';
import * as lunr from 'lunr';

interface LunrStore<T> {
  [propName: string]: T;
}

interface LunrLoader<T> {
  en: {index: lunr.Index; store: LunrStore<T>};
}

declare global {
  interface Window {
    __LUNR__: {
      __loaded: Promise<LunrLoader<any>>;
    };
  }
}

export async function loader<T>(): Promise<Option<LunrLoader<T>>> {
  return optionify(window.__LUNR__)
    .async()
    .mapNullable(async lunr => lunr.__loaded);
}

export interface SearchResult {
  title: string;
  url: string;
  ref: string;
}

export default function(
  index: lunr.Index,
  store: LunrStore<SearchResult>,
  query: string,
): SearchResult[] {
  return index
    .query(q => {
      const tokens = lunr.tokenizer(query);

      q.term(tokens, {boost: 100, usePipeline: true});
      q.term(tokens, {
        boost: 10,
        usePipeline: false,
        wildcard: lunr.Query.wildcard.TRAILING,
      });
      q.term(tokens, {boost: 1});
    })
    .map(({ref}) => ({ref, ...store[ref]}));
}
